<template>
  <div class="wrapper" :class="{ 'nav-open': $sidebar.showSidebar }">
    <side-bar
      :background-color="sidebarBackground"
      short-title="Argon"
      title="Argon"
    >
      <template slot="links">
        <sidebar-item
          :link="{
            name: 'Dashboard',
            icon: 'ni ni-tv-2 text-red',
            path: '/dashboard'
          }"
        />

        <sidebar-item :link="{name: 'Banks', icon: 'ni ni-building text-blue', path: '/banks'}"/>
        <sidebar-item :link="{name: 'Bank Accounts', icon: 'ni ni-credit-card text-green', path: '/bank/accounts'}"/>
        <sidebar-item :link="{name: 'Cheques', icon: 'fas fa-money-check text-yellow', path: '/bank/cheque'}"/>
        <sidebar-item v-if="userRole == 'Administrator'" :link="{name: 'Users', icon: 'fas fa-users text-orange', path: '/users'}"/>

      </template>
    </side-bar>
    <div class="main-content" :data="sidebarBackground">
      <dashboard-navbar></dashboard-navbar>

      <div @click="toggleSidebar">
        <fade-transition :duration="200" origin="center top" mode="out-in">
          <!-- your content here -->
          <router-view></router-view>
        </fade-transition>
        <content-footer v-if="!$route.meta.hideFooter"></content-footer>
      </div>
    </div>
  </div>
</template>
<script>
  import DashboardNavbar from './DashboardNavbar.vue';
  import ContentFooter from './ContentFooter.vue';
  import { FadeTransition } from 'vue2-transitions';
  import { api } from '@/main';

  export default {
    components: {
      DashboardNavbar,
      ContentFooter,
      FadeTransition
    },
    data() {
      return {
        sidebarBackground: 'vue', //vue|blue|orange|green|red|primary
        username: '',
        userRole: ''
      };
    },
    async mounted(){
      this.getUserRole();
    },
    methods: {
      toggleSidebar() {
        if (this.$sidebar.showSidebar) {
          this.$sidebar.displaySidebar(false);
        }
      },
      getUserRole(){
        let self = this;
        self.username = localStorage.getItem('aisname');

        fetch(api + '/users/role/' + self.username).then(function(response){
          return response.json();
        }).then(function(response) {
          if(response.success) {
            self.userRole = response.output;
            console.log(response.output);
          }
        }).catch(function(error) {
          console.log(error);
          self.loadError = true;
        });
      }
    }
  };
</script>
