import Vue from 'vue'
import Router from 'vue-router'
import DashboardLayout from '@/layout/DashboardLayout'
import AuthLayout from '@/layout/AuthLayout'
Vue.use(Router)

export default new Router({
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      redirect: 'dashboard',
      component: DashboardLayout,
      children: [
        {
          path: '/dashboard',
          name: 'dashboard',
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "demo" */ './views/MainDashboard.vue')
        },
        {
          path: '/banks',
          name: 'banks',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/Bank.vue')
        },
        {
          path: '/bank/accounts',
          name: 'bank accounts',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/BankAccounts.vue')
        },
        {
          path: '/bank/cheque',
          name: 'cheque',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/BankCheques.vue')
        },
        {
          path: '/cheque/series',
          name: 'cheque series',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/ChequeSeries.vue')
        },
        {
          path: '/new/cheque',
          name: 'new cheque',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/NewCheque.vue')
        },
        {
          path: '/update/cheque/:id',
          name: 'update cheque',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/UpdateCheque.vue')
        },
        {
          path: '/view/cheque/:id',
          name: 'view cheque',
          component: () => import(/* webpackChunkName: "demo" */ './views/Banks/ViewCheque.vue')
        },
        {
          path: '/users',
          name: 'Users',
          component: () => import(/* webpackChunkName: "demo" */ './views/Users/users.vue')
        },
        {
          path: '/new/user',
          name: 'New User',
          component: () => import(/* webpackChunkName: "demo" */ './views/Users/NewUser.vue')
        }
      ]
    },
    {
      path: '/',
      redirect: 'login',
      component: AuthLayout,
      children: [
        {
          path: '/login',
          name: 'login',
          component: () => import('./views/Users/userLogin.vue')
        },
        {
          path: '/register',
          name: 'register',
          component: () => import('./views/Users/userRegister.vue')
        }
      ]
    },
    
  ]
})
